body {
  background-color: #f1f2ff;
}
.App {
  font-family: "Roboto", sans-serif;
  text-align: center;
  letter-spacing: 0.3px;
}
label {
  display: block;
  margin-bottom: 0.5em;
}
h1 {
  color: #06060c;
  font-weight: 600;
  margin: 60px 0 -4px 0;
}
h2 {
  color: #393b4c;
  font-size: 18px;
  font-weight: 300;
  margin-bottom: 8px;
}
.uploadImg {
  width: 60px;
  height: auto;
}
.larryimg {
  width: 100px;
  height: auto;
}
input[type="file"] {
  display: none;
}
.uploadFile {
  width: 400px;
  padding: 12px;
  margin: 36px 0 28px 0;
  border: .5px solid #9296b3;
  border-radius: 8px;
  color: #06060c;
  background-color: #e1e4fb;
  box-shadow: 3px 3px 8px #e1e4fb;
  font-size: 16px;
  font-weight: 600;
  font-family: "Roboto", sans-serif;
  display: inline-block;
  cursor: pointer;
  transition: color 0.5s ease;
  transition: background-color 0.5s ease;
}
.uploadFile:hover {
  color: #393b4c;
  background-color: #d0d4f6;
}
#metaTitle,
#metaDescription,
#metaKeyword,
#addMeta {
  border-radius: 8px;
  padding: 12px;
  margin: 2px 0;
  font-family: "Roboto", sans-serif;
  font-size: 16px;
  box-shadow: 3px 3px 8px #e1e4fb;
  transition: box-shadow 0.5s ease;
}
#metaTitle,
#metaDescription,
#metaKeyword {
  color: #393b4c;
  width: 400px;
  font-weight: 300;
  border: none;
  background-color: #fff;
}
#metaTitle::placeholder,
#metaDescription::placeholder,
#metaKeyword::placeholder {
  color: #9296b3;
}
#metaTitle:focus,
#metaDescription:focus,
#metaKeyword:focus {
  border: none;
  outline: none;
  box-shadow: 3px 3px 8px #d7dbff;
}
#metaKeyword {
  height: 100px;
  overflow: auto;
  resize: none;
}
#addMeta {
  width: 200px;
  margin: 20px 0 50px 0;
  color: #fff;
  font-weight: 600;
  letter-spacing: 0.3px;
  border: none;
  background-color: #06060c;
  cursor: pointer;
  transition: color 0.5s ease;
  transition: background-color 0.5s ease;
}
#addMeta:hover {
  color: #f1f2ff;
  background-color: #393b4c;
}
.Footer {
  height: 16px;
  width: 100%;
  left: 0;
  bottom: 0;
  position: fixed;
  padding: 10px 0 16px 0;
  text-align: center;
  background-color: #9296b3;
}
#copyright {
  position: relative;
  display: inline-block;
  font-size: 13px;
  color: #393b4c;
}
button {
  position: relative;
  display: inline-block;
  background: none;
	border: none;
  padding: 0;
  font-size: 13px;
  text-decoration: underline;
	cursor: pointer;
  transition: color 0.5s ease;
}
button:hover {
  color: #393b4c;
}
.popup {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  background-color: rgba(0,0,0, 0.5);
}
.popup_inner {
  position: absolute;
  left: 25%;
  right: 25%;
  top: 15%;
  bottom: 15%;
  margin: auto;
  border-radius: 20px;
  background: white;
}
.popup-header {
  margin: 40px;
}
